@font-face {
  font-family: "FuturaPT-Bold";
  src: url("../fonts/FuturaPT-Bold.woff2") format("woff2"), url("../fonts/FuturaPT-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: "normal";
}

@font-face {
  font-family: "FuturaPT-Book";
  src: url("../fonts/FuturaPT-Book.woff2") format("woff2"), url("../fonts/FuturaPT-Book.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: "normal";
}

@font-face {
  font-family: "FuturaPT-Medium";
  src: url("../fonts/FuturaPT-Medium.woff2") format("woff2"), url("../fonts/FuturaPT-Medium.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: "normal";
}

:root {
  --color-bg: #fff;
}

/* .light-theme {
	--color-bg: #fff;
}

.dark-theme {
	--color-bg: #191919;
} */
.btn-reset, .link-reset, .input-reset, .list-reset, .text-reset {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*::before,
*::after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

.html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
  font-size: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

.body {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: var(--color-bg);
  font-family: "FuturaPT", sans-serif;
  font-size: 18px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

.header {
  flex: 0 0 auto;
}

.main-content {
  flex: 1 0 auto;
  overflow: hidden;
}

.footer {
  flex: 0 0 auto;
}

.lock {
  overflow: hidden;
}

.ios-lock {
  overflow: hidden;
  position: relative;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.container {
  max-width: 1522px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

@media (max-width: 991.98px) and (orientation: portrait) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (max-width: 767.98px) and (orientation: portrait) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (max-height: 479.98px) and (max-width: 1023px) and (orientation: landscape) {
  .container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

/*! Basic */
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px !important;
  width: 1px !important;
  padding: 0 !important;
  border: 0 !important;
  overflow: hidden;
}

/*! Reset classes */
.btn-reset {
  width: auto;
  margin: 0;
  padding: 0;
  background: transparent;
  outline: 0;
  color: inherit;
  font-family: inherit;
  font: inherit;
  cursor: pointer;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

.btn-reset::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.btn-reset:focus {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.btn-reset:focus-visible {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.btn-reset:focus:not(:focus-visible) {
  box-shadow: none;
  border-radius: 0;
}

.link-reset {
  outline: 0;
  color: inherit;
  text-decoration: none;
}

.link-reset:visited {
  text-decoration: none;
}

.link-reset:focus {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.link-reset:focus-visible {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.link-reset:focus:not(:focus-visible) {
  box-shadow: none;
  border-radius: 0;
}

.input-reset {
  outline: 0;
  -webkit-appearance: none;
}

.input-reset::-ms-clear {
  display: none;
}

.input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

.input-reset:focus {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.input-reset:focus-visible {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.input-reset:focus:not(:focus-visible) {
  box-shadow: none;
  border-radius: 0;
}

.list-reset {
  text-indent: 0;
  list-style: none;
  list-style-type: none;
}

.text-reset {
  font-size: inherit;
  font-weight: 400;
  font-style: normal;
}

/*! Main classes */
.main-heading {
  font-size: 64px;
  font-weight: 700;
}

@media (max-width: 1507px) {
  .main-heading {
    font-size: 45px;
  }
}

@media (max-width: 991.98px) {
  .main-heading {
    font-size: 35px;
  }
}

@media (max-width: 767.98px) {
  .main-heading {
    font-size: 30px;
  }
}

@media (max-width: 479.98px) {
  .main-heading {
    font-size: 20px;
  }
}

.main-text {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.1;
}

